body{
	margin: 0;
	padding: 0;
	background-color: rgba(246, 249, 252, 1);
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: antialiased;
}

code{
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
	monospace;
}

/* General */

.App{
  text-align: center;
}

/* Banner */

.bannerContainer{
	width: 100%;
	position: sticky;
	position: -webkit-sticky;
	top: 0;
	background-color: white;
}

.bannerBar{
    height: 1px;
    width: 100%;
	min-width: 1000px;
    background: -webkit-linear-gradient(300deg, navy -15%, #05d5ff 80%, #99ffcc 100%);
    background: linear-gradient(150deg, navy -15%, #05d5ff 80%, #99ffcc 100%);
	box-shadow: 0px 1px 2px rgba(0,0,0,.2);
}

.bannerTitlesContainer{
	width: 100%;
	min-width: 1000px;
	text-align: center;
	-webkit-animation: reveal-banner 1s 0.25s ease-in-out;
	animation: reveal-banner 1s 0.25s ease-in-out;
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
}

.pearsonalLogo{
	margin-top: 10px;
	margin-bottom: 5px;
	color: black;
	font-size: 40px;
	font-family: bitter;
	font-weight: normal;
	text-transform: uppercase;
	-webkit-transition: 0.2s;
	transition: 0.2s;
	display: inline-block;
}

.pearsonalLogo:hover{
	margin-top: 10px;
	margin-bottom: 5px;
	color: #c82714;
	font-size: 40px;
	font-family: bitter;
	font-weight: normal;
	-webkit-transition: 0.2s;
	transition: 0.2s;
	display: inline-block;
	transition: 0.2s;
}

.bannerSubtitle{
	width: 100%;
	min-width: 1000px;
	text-align: center;
	padding-bottom: 5px;
	font-size: 16px;
	font-family: bitter;
	text-transform: lowercase;
	font-weight: 300;
	font-style: italic;
	color: dimgrey;
	display: inline-block;
}

/* Navigation */

.navigationContainer{
	width: 100%;
	min-width: 1000px;
	margin: 0 auto;
	padding: 0;
	text-align: center;
	list-style-type: none;
	display: inline-block;
}

li{
	display: inline-block;
}

.noStyling{
	text-decoration: none;
	color: inherit;
}

.navButtons{
	cursor: pointer;
	min-width: 150px;
	padding-top: 10px;
	padding-bottom: 10px;
	text-align: center;
	font-family: bitter;
	font-weight: 400;
}

.navigationHome:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: navy;
	background-color: #BEBEED;
}

.navigationCards:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: darkgreen;
	background-color: #AFDFAF;
}

.navigationProjects:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: indigo;
	background-color: #DAA7FF;
}

.navigationAbout:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: #776505;
	background-color: #EAD878;
}

.navigationContact:hover{
	-webkit-transition: 0.2s;
	transition: 0.2s;
	color: firebrick;
	background-color: #FFC2C2;
}

/* Footer */

.footerContainer{
	width: 1000px;
	min-width: 1000px;
	margin: 0 auto;
}

.footerText{
	padding: 15px;
	margin: 0 auto;
	font-family: bitter;
	font-size: 14px;
	color: dimgrey;
	text-align: center;
}

/* Ritigos */

.ritigoContainer{
	/* min-width: 1000px !important; */
}

.ritigo{
	-webkit-transform: skewY(-12deg);
	transform: skewY(-12deg);
	background-clip: border-box;
	box-sizing: border-box;
	position: absolute;
	display: block;
	z-index: -100;
}

.upper{
	height: 80px;
	width: 200px;
	top: 30%;
}

.middle{
	height: 100px;
	width: 500px;
	top: 50%;
}

.lower{
	height: 120px;
	width: 300px;
	top: 80%;
}

.upperRight{
	height: 120px;
	width: 300px;
	top: 20%;
	right: 0;
}

.middleRight{
	height: 100px;
	width: 500px;
	top: 50%;
	right: 0;
}

.lowerRight{
	height: 80px;
	width: 200px;
	top: 80%;
	right: 0;
}

/* Home */

.homeBackground{
	min-width: 840px;
	padding: 80px;
}

.homeBlurbContainer{
	width: 500px;
	min-width: 600px;
	min-height: 600px;
	margin: 0 auto;
	padding: 75px;
	border: 0.5px solid lightgrey;
	border-radius: 5px;
	background-color: white;
}

.homeBlurb{
	font-family: helvetica;
	font-size: 20px;
	font-weight: 300;
	line-height: 1.5;
	white-space: pre-wrap;
}

.ritigoLightBlue{
	background: -webkit-linear-gradient(left, #c1dcff 0%, #a0c9ff 100%);
	background: linear-gradient(90deg, #c1dcff 0%, #a0c9ff 100%);
}

.ritigoMediumBlue{
	background: -webkit-linear-gradient(left, #82b8ff 0%, #59a0ff 100%);
	background: linear-gradient(90deg, #82b8ff 0%, #59a0ff 100%);
}

.ritigoDarkBlue{
	background: -webkit-linear-gradient(left, #3088ff 0%, #0049ad 100%);
	background: linear-gradient(90deg, #3088ff 0%, #0049ad 100%);
}

/* Cards */


.cardsSummary{
	padding: 80px;
	text-align: center;
	font-family: tangerine;
	font-size: 24px;
}

.cardsContainer{
	min-height: 1000px;
	width: 1000px;
	min-width: 1000px;
	margin: 0 auto;
	border-radius: 5px;
}

.cardLayout{
	float: left;
	background-color: rgba(255,255,255, 1);
	text-align: center;
}

.cardLayout > div {
	display: inline-block;
}

.cardCover{
	height: auto;
	width: 250px;

}

.cardInside{
	height: auto;
	width: 250px;
}

.cardYear{
	text-align: center;
	font-family: bitter;
	font-size: 36px;
}

.cardDescription{
	display: inline-block;
}

.ritigoLightRed{
	background: -webkit-linear-gradient(left, #fc6a6a 0%, #f70202 100%);
	background: linear-gradient(90deg, #fc6a6a 0%, #f70202 100%);
}

.ritigoMediumGreen{
	background: -webkit-linear-gradient(left, #99ed68 0%, #4dc076 100%);
	background: linear-gradient(90deg, #99ed68 0%, #4dc076 100%);
}

.ritigoDarkGreen{
	background: -webkit-linear-gradient(left, #05af41 0%, #007f2d 100%);
	background: linear-gradient(90deg, #05af41 0%, #007f2d 100%);
}

/* Projects  */

.ritigoLightPurple{
	background: -webkit-linear-gradient(left, #ebd1ff 0%, #dcb3fc 100%);
	background: linear-gradient(90deg, #ebd1ff 0%, #dcb3fc 100%);
}

.ritigoMediumPurple{
	background: -webkit-linear-gradient(left, #c77aff 0%, #b44efc 100%);
	background: linear-gradient(90deg, #c77aff 0%, #b44efc 100%);
}

.ritigoDarkPurple{
	background: -webkit-linear-gradient(left, #7a00bc 0%, #6b00a5 100%);
	background: linear-gradient(90deg, #7a00bc 0%, #6b00a5 100%);
}

/* About */


.ritigoLightYellow{
	background: -webkit-linear-gradient(left, #fff0aa 0%, #ffea87 100%);
	background: linear-gradient(90deg, #fff0aa 0%, #ffea87 100%);
}

.ritigoMediumYellow{
	background: -webkit-linear-gradient(left, #fce197 0%, #fcd669 100%);
	background: linear-gradient(90deg, #fce197 0%, #fcd669 100%);
}

.ritigoDarkYellow{
	background: -webkit-linear-gradient(left, #ffd400 0%, #ffd400 100%);
	background: linear-gradient(90deg, #ffd400 0%, #ffd400 100%);
}

/* 404 Not Found */

.notFound{
	width: 600px;
	padding: 100px;
	margin-top: 300px !important;
	margin: 0 auto;
	font-size: 40px;
	color: dimgrey;
	background-color: rgba(255,255,255, 1);
	border: 0.5px solid lightgrey;
	border-radius: 5px;
	font-family: bitter;
	text-align: center;
}

.ritigoLightGrey{
	background: gainsboro;
}

.ritigoMediumGrey{
	background: silver;
}

.ritigoDarkGrey{
	background: grey;
}

