/* Cards */


.cardsSummary{
	padding: 80px;
	text-align: center;
	font-family: tangerine;
	font-size: 24px;
}

.cardsContainer{
	min-height: 1000px;
	width: 1000px;
	min-width: 1000px;
	margin: 0 auto;
	border-radius: 5px;
}

.cardLayout{
	float: left;
	background-color: rgba(255,255,255, 1);
	text-align: center;
}

.cardLayout > div {
	display: inline-block;
}

.cardCover{
	height: auto;
	width: 250px;

}

.cardInside{
	height: auto;
	width: 250px;
}

.cardYear{
	text-align: center;
	font-family: bitter;
	font-size: 36px;
}

.cardDescription{
	display: inline-block;
}

.ritigoLightRed{
	background: linear-gradient(90deg, #fc6a6a 0%, #f70202 100%);
}

.ritigoMediumGreen{
	background: linear-gradient(90deg, #99ed68 0%, #4dc076 100%);
}

.ritigoDarkGreen{
	background: linear-gradient(90deg, #05af41 0%, #007f2d 100%);
}
